___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: 'Segoe UI', sans-serif;

  background-color: #313131;

  margin: 0;
}

h1 {
  font-size: 3rem;
  font-weight: 700;
  color: #f9f9f9;
  margin-left: 5px;
}

.descricao {
  font-size: 22px;
  font-weight: 600;
  color: #f9f9f9;
  margin-left: 5px
}

.container-formulario {
  display: flex;
  margin-left: 5px;
  gap: 5px;
}

.pesquisa {
  outline: 1px solid #f9f9f9 important;

  border:none;
  border-radius: 10px;

  font-size: 18px;

  padding: 5px;
}

.btn {
  display: grid;
  padding: 15px;

  border: none;
  border-radius: 10px;
}

.btn:hover {
  opacity: 0.9;
  cursor: pointer;
  outline: 1px solid #ffffff;
}

.btn-pesquisar {
  background-color: #4DAD5B;
  color: white;
}

.btn-limpar {
  background-color: #DA471B;
  color: white;
}

.card-pokemon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;

  max-width: 350px;
  height: 450px;

  background-color: #e1e1e1;
  color: #313131;

  border-radius: 1rem;

  margin-top: 5%;
}

.pnlPokemon {
  background-color: #bebebe;
  display: flex;
  gap: 2px;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 150px;
}

.pnlPokemon:hover {
  cursor: pointer;
  outline: 1px solid blue;
}

.grid-pokemons {
  width: 80%;
  display: flex;
  gap: 2px;
  margin: auto;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 20px;
}

.notificacao {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;

  margin-left: auto;
  margin-right: auto;
  
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  
  background-color: #DA471B;
  border-radius: 10px;

  width: 300px;

  padding: 10px 20px;
  margin-bottom: 1%;

}

.notificacao:hover {
  opacity: 0.9;
  cursor: pointer;
